/*style page domaines de compétence*/

.competences {
    display: block;
    background-color: $color-2;
    padding: 0px;
    margin: 0px;
    &__content {
        display: block;
        margin: 50px;
    }
}

/*Organisation de l'article contenant les différents domaines*/
.domaines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 50px;
    margin-left: 25px;
    &__items {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 50px;
        &__text {
            display: flex;
            flex-direction: column;
            margin-left: 50px;
            align-items: flex-start;
            width: 70%;
            &__p {
                text-align: justify;
                font-size: 18px;
                font-family: "Lora";
                margin-bottom: 10px;
                &--gras {
                    font-weight: 800;
                }

                ul {
                    list-style: inside;
                    margin-left: 25px;
                }
            }
            &__a {
                font-size: 16px;
                font-family: "Lora";
                font-style: italic;
                color: $color-1;
                &:hover {
                    cursor: pointer;
                    color: $color-1;
                }
                &:visited {
                    cursor: pointer;
                    color: $color-1;
                }
            }
        }
    }
}

.all-exp {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /*TOUS LES ITEMS : réalisations oc, autres réalisations, postes occupés etc...*/
    &__items {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 50px;
        width: 100%;
    }

    &__dispo-postes {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        margin-top: 50px;
        &__texte {
            margin-top: 50px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
            width: 600px;
            margin-left: 0px;
            &--small {
                justify-content: flex-start;
            }
            &__intitule {
                &--space {
                    margin-bottom: 50px;
                }
                &__desc {
                    margin-left: 20px;
                    margin-bottom: 50px;
                    text-align: justify;
                }
            }
        }
    }
}

@media (max-width: 960px) {
    .competences {
        display: block;
        background-color: $color-2;
        padding: 10px;
        &__content {
            margin: 50px 10px 50px 10px;
        }
    }
    .domaines {
        margin-left: 0px;
        &__items {
            display: flex;
            flex-direction: column-reverse;
            justify-content: flex-start;
            align-items: center;
            margin-left: 0px;
            margin-bottom: 50px;
            &__text {
                width: 100%;
                margin-left: 0px;
                margin-bottom: 25px;
            }
        }
    }
    .all-exp {
        &__dispo-postes {
            margin: 0px;
            &__texte {
                margin: 25px 0px 25px 0px;
                max-width: 300px;
            }
        }
    }
}
