.dropdown-menu {
    width: 320px;
    position: absolute;
    top: 80px;
    list-style: none;
    text-align: start;
    z-index: 2;
}

.dropdown-menu li {
    background: $color-1-transparent;
    cursor: pointer;
}

.dropdown-menu li:hover {
    background: $color-1-lighten;
}

.dropdown-menu.clicked {
    display: none;
}

.dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 16px;
}

@media screen and (max-width: 960px) {
    .fa-caret-down {
        display: none;
    }
}
