header {
}

.navbar {
    background: $color-1;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: $font-1;
    z-index: 2;
}

.navbar-logo {
    color: #fff;
    cursor: pointer;
    text-decoration: none;
    font-size: 28px;
    margin-left: 30px;
}

.fa-caret-down {
    margin-left: 10px;
    font-size: 20px;
}

.fa-firstdraft {
    margin-left: 10px;
    font-size: 12px;
}

.nav-menu {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    text-align: center;
    justify-content: end;
    font-size: 20px;
    margin-right: 20px;

    .nav-item {
        display: flex;
        align-items: center;
        height: 80px;
        margin: 0px 10px 0px 10px;
    }
}

.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    background-color: $color-1;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;

        .nav-item {
            margin: 0px;
        }
    }

    .nav-menu.active {
        background: $color-1;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        display: flex;
        text-align: center;
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        margin: 0;
    }

    .nav-links:hover {
        background-color: $color-1-lighten;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(5%, 50%);
        margin: 0px;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 28px;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 30px;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 28px;
        margin: 32px auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        color: #fff;
        font-size: 28rem;
    }

    .nav-links-mobile:hover {
        background: #fff;
        transition: 250ms;
    }
}
