.formation {
    width: 80%;
    display: flex;
    flex-direction: column;
    margin: 50px 0px 50px 0px;
}

.diplomes {
    display: flex;
    flex-direction: column-reverse;
    &__bloc {
        margin-top: 50px;
        color: $color-2;
        display: flex;
        flex-direction: column;
        align-items: center;

        &__date {
            color: $color-1;
            font-size: 20px;
            margin-bottom: 15px;
        }
        &__diplome {
            width: 500px;
            background-color: $color-1;
            border-radius: 20px;
            padding: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            &__text {
                margin: 10px 0px 10px 0px;
                &--type {
                }
                &--name {
                    font-size: 28px;
                    font-weight: 600;
                    text-align: center;
                }
                &--school {
                    text-align: center;
                }
            }
            &:hover {
                cursor: pointer;
                transform: scale(1.08);
                border: 2px solid $color-1;
                color: $color-1;
                background-color: $color-2;
            }
        }
    }
}

@media (max-width: 600px) {
    .diplomes {
        &__bloc {
            &__date {
                margin-bottom: 25px;
            }
            &__diplome {
                width: 300px;
            }
        }
    }
}
