.footer {
    background: $color-1;
    color: white;
    font-family: $font-1;
    width: 100%;
    bottom: 0;
    a {
        color: $color-2;
        text-decoration: none;
    }
    &__main-part {
        box-sizing: border-box;
        padding: 25px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;
        &__icons {
            width: 50%;
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-end;
            justify-content: space-between;
            &__pages {
                width: 160px;
                display: flex;
                flex-direction: row;
                justify-content: space-around;
                align-items: center;
            }
        }
        &__plan {
            width: 50%;
            box-sizing: border-box;
            &__ul {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                margin: 15px 0px 0px 0px;
                list-style: none;
                &__p-w {
                    font-size: 18px;
                    margin: 0px;
                    color: $color-2;
                }
                &__li {
                    font-size: 16px;
                    font-family: "PlayFair Display";
                    &:hover {
                        font-weight: 600;
                    }
                }
            }
        }
    }
    &__p-w-s {
        padding: 0px;
        text-align: center;
        font-size: 12px;
        font-family: "Lora";
        margin: 10px 0px 10px 0px;
        color: $color-2;
    }
}

.fa-brands {
    font-size: 24px;
    transition: transform 300ms ease-in-out;
}

.fa-brands:hover {
    transform: scale(1.2);
}

.p-lora-w {
    color: $color-2;
    font-size: 16px;
    margin: 0px;
}

@media (max-width: 960px) {
    .footer {
        padding-top: 25px;
        padding-left: 50px;
        padding-right: 50px;
        &__main-part {
            flex-direction: column-reverse;
            align-items: flex-start;
            justify-content: flex-start;
            padding: 0px;
            &__icons {
                align-items: flex-start;
                margin-bottom: 50px;
            }
            &__plan {
                &__ul {
                    padding: 0px;
                    flex-direction: column;
                    align-items: flex-start;
                    margin-bottom: 50px;
                    &__li {
                        margin: 0px;
                        margin-bottom: 10px;
                    }
                    &__p-w {
                        display: none;
                    }
                }
            }
        }
    }
}

@media (min-width: 960px) {
    .footer {
        &__main-part {
            &__plan {
                width: 35%;
            }
        }
    }
}
