.home {
    width: 100%;
    height: 100%;
    background-image: url("../../assets/images/img-1.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    &__content {
        margin: 0px;
        padding: 50px;
        &__intro {
            display: flex;
            flex-direction: row;
            text-align: justify;
            &__text {
                width: 65%;
                p {
                    margin-top: 25px;
                }
            }
            &__photo {
                width: 35%;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;

                &__img {
                    width: 150px;
                    height: 150px;
                    border-radius: 50%;
                }
            }
        }
        &__competences {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin: 150px 0px 50px 0px;
        }
    }
}

@media (max-width: 960px) {
    .home {
        &__content {
            display: flex;
            flex-direction: column;
            &__intro {
                width: 100%;
                text-align: justify;
                flex-direction: column-reverse;
                justify-content: center;
                align-items: center;
                margin-bottom: 50px;
                &__text {
                    width: 100%;
                    margin-top: 25px;
                }
                &__photo {
                    width: 100%;
                    margin: 25px 0px 0px 0px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &__img {
                        margin: 25px 0px 0px 0px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                }
            }
            &__competences {
                flex-direction: column;
                justify-content: space-between;
                margin: 0px 0px 0px 0px;
            }
        }
    }
}
