.c-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    &:hover {
        cursor: pointer;
    }
    &--1 {
        margin-bottom: 100px;
    }
    &--2 {
        margin-top: 100px;
    }
    &__img {
        position: absolute;
    }
    &__cercle {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 220px;
        height: 220px;
        border-radius: 50%;
        border: 3px solid $color-3;
        &__content {
            opacity: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 200px;
            height: 200px;
            border-radius: 50%;
            background-color: $color-4;
            &__ul {
                list-style: none;
                text-align: center;
                color: $color-2;
                font-family: $font-2;
                font-size: 18px;
                font-weight: 200;
            }
        }
    }
}

.c-item:hover h2 {
    color: $color-2-transparent;
}

.c-item:hover .c-item__cercle {
    border: 3px solid $color-2-transparent;
}

.c-item:hover .c-item__cercle__content {
    animation: text_competence 350ms ease-in;
    animation-fill-mode: forwards;
}

@media (max-width: 960px) {
    .c-item {
        &--1 {
            margin-bottom: 25px;
        }
        &--2 {
            margin-bottom: 25px;
            margin-top: 0px;
        }
    }
}
