.btn-cv {
    font-family: $font-2;
    font-size: 14px;
    font-weight: 400;
    margin-top: 15px;
    padding: 8px 15px 8px 15px;
    color: $color-2;
    background-color: $color-3-transparent;
    border: 0px solid;
    border-radius: 10px;
    &:hover {
        cursor: pointer;
        color: $color-3-transparent;
        background-color: $color-2-transparent;
        font-weight: 800;
        border: 0px solid $color-2;
    }
}
